// @file
// Animation and easing

//F1 site easing
$ease: cubic-bezier(.13, .94,.18, 1);
$ease-smooth: cubic-bezier(.23,.61,.41, 1);

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes slideIn {
  0% {
    transform: translateY(8%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes slideFadeinBig {
  0% {
    opacity: 0;
    transform: translateX(40px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}




@keyframes slideFadein {
  0% {
    opacity: 0;
    transform: translateX(10px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
