.process {
  padding: 0 0;
  h2.kicker {
    padding: 5rem 10rem;
  }
}


.mosiac {
  background: #ffffff;
  box-shadow: shadow('low');
  position: relative;
  width: 100%;
  padding: 0;

  @include breakpoint(bp('tablet')) {
    display: flex;
  }

  .artifact-item {
    @include breakpoint(max-width bp('tablet')) {
      font-size: .8rem;
    }
  }

  .artifact__inner-row {
    .artifact-item {
      @include breakpoint(bp('tablet')) {
        font-size: .8rem;
      }
    }
  }

  .artifact__row {
    @include breakpoint(bp('tablet')) {
      display: flex;
      flex: 1 1 auto;
      flex-direction: column;
      width: 50%;
    }
    .artifact__inner-row {
      @include breakpoint(bp('desktop')) {
        display: flex;
        flex-direction: row;
        flex: 1 0 auto;

        .artifact-item {
          flex: 1 1 auto;
          width: 50%;
        }
      }
    }
  }
}

%node-promo-tile {
  cursor: pointer;
  background: #ffffff;
  position: relative;
  overflow: hidden;

  .mask {
    opacity: 0;
    transition: opacity 300ms ease-in-out;
  }

  .node__title {
    a {
      color: #ffffff;
      text-decoration: none;
    }
  }

  .button--more {
    opacity: 0;
    transition: opacity 300ms ease-in-out 300ms, background-color 250ms ease-in-out 0s;
  }

  .artifact-item__text {
    padding: .25em 1em;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: zIndex('high');
  }

  .artifact-item__image {
    @include bg-set;
    background-position: center bottom;
    bottom: 0;
    cursor: pointer;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    transform: scale(0.8);
    transition: transform 550ms ease-in-out;
  }

  &:hover,
  &:focus,
  &:active {
    .mask {
      opacity: .75;
    }
    .button--more {
      opacity: 1;
    }
    .artifact-item__image {
      transform: scale(1);
    }
  }

}

$major-height: 60vh;
$major-height-mega: 80vh;
$sub-height: $major-height/2;
$sub-height-mega: $major-height-mega/2;
$mod-padding: 2rem;
.artifact-item {
  @extend %node-promo-tile;
  height: rem(200px);
  overflow: hidden;
  position: relative;

  @include breakpoint(bp('tablet')) {
    height: $sub-height;
    min-height: rem(300px);
  }

  @include breakpoint(bp('mega')) {
    height: $sub-height-mega;

  }

  .artifact-item__text {
    @include grid-block__text();
    padding: 0 2em;
  }

  &.is-large {
    flex: 1 0 auto;

    .artifact-item__text {
      padding: 0 2em;
    }

    @include breakpoint(bp('tablet')) {
      height: $major-height;
      min-height: rem(600px);

    }
    @include breakpoint(bp('desktop')) {
      .artifact-item__text {
        padding: 0 8em;
      }
    }

    @include breakpoint(bp('mega')) {
      height: $major-height-mega;
    }

  }
}
