.work {
  padding: 0 0;
  h2.kicker {
    padding: 3rem 2rem;
    @include clearfix;

    @include breakpoint(bp('tablet')) {
        padding: 5rem;
    }
    @include breakpoint(bp('desktop')) {
        padding: 10rem;
    }
  }
}

.work-item {
  @extend %node-promo-tile--alt;
  @include grid-block(375px);
  background: #000;
  width: 100%;

  @include breakpoint(bp('tablet')) {
    @include grid-block(500px);
    float: left;
    width: percentage(1/2);
  }

  &.fb {
    @include breakpoint(bp('tablet')) {
      @include grid-block(1200px);
      float: left;
      width: 100%;
    }
  }

  .work-item__text {
    @extend %grid-block__text;
    transition: opacity 225ms $ease-smooth;
    padding: 0 5em;
    opacity: 1;

    @include breakpoint(bp('desktop')) {
      padding: 0 10em;
      opacity: 0;
    }

    .work-item__kicker {
      color: $white;
      text-transform: uppercase;
      font-weight: 300;
      font-size: 0.875rem;
      letter-spacing: 0.0125rem;
    }
    h2.work-item__title {
      color: $white;
    }
  }

  .work-item__image {
    @include bg-set;
    bottom: 0;
    cursor: pointer;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    transform: scale(1.02);
    transition: transform 550ms ease-in-out;
  }

  .mask {
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 2;
    background: #000;
    transition: all 525ms $ease-smooth;
    opacity: .6 ;
  }

  &:nth-of-type(odd) {
    .mask {
      opacity: .4;
    }
  }
  &:nth-of-type(even) {
    .mask {
      opacity: .6;
    }
  }

  &:hover,
  &:focus,
  &:active {
    .mask {
      background: $red;
      opacity: .9;
    }
    .button--more {
      opacity: 1;
    }
    .work-item__text {
      opacity: 1;
    }
    .work-item__image {
      transform: scale(1.2);
    }
  }

  &.more {
    background: $white;
    .mask {
      background: $red;
      opacity: .9;
    }
    .work-item__text {
      opacity: 1;
    }
  }
}

// Work Detail
.the-facts {
  max-width: $max-width;
  margin: 0 auto;
  @include add-grid(12);
  @include add-gutter(0);
  @extend %clearfix;

  .section__content,
  .project__description {
    @include breakpoint(bp('tablet')) {
      @include grid-span(9, 1);
    }
  }

  .section__sidebar,
  .project__metadata {
    @include breakpoint(bp('tablet')) {
      @include grid-span(3, 10);
    }
  }

  h1.project__title {
    border-bottom: 2px solid $red;
    padding: 1rem 0;
    margin-bottom: 0;
    text-transform: none;
    font-size: 3.5rem;
    font-weight: 200;
  }

  .project__description {
    padding: 2rem 2rem 1rem 0rem;
    box-sizing: border-box;
    .intro {
      margin-top: 0;
    }
  }
  .project__metadata {
    .partner__discipline {
      border-bottom: 2px solid $red;
      padding: 1rem 0.5rem;
      .value {
        font-size: 0.875rem;
      }
      &:last-of-type {
        border-bottom: none;
      }
    }
    @include breakpoint(bp('tablet')) {
      border-left: 2px solid $red;
    }
  }
  .key {
    font-size: 0.75rem;
    font-weight: 600;
    margin-bottom: 1rem;
    text-transform: uppercase;
    display: block;
    color: $red;
  }
  .value {
    display: block;
  }
}


// Detail Images
.work-artifacts {
  padding: 5rem 0;
}
.feature {
  @extend %clearfix;
  position: relative;
  height: auto;
  @include breakpoint(bp('desktop')) {
    height: 570px;
  }
}

.feature__image {
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: top center;
  height: 500px;
  position: relative;
  @include breakpoint(bp('desktop')) {
    background-size: cover;
    height: 570px;
  }
}

.feature__text {
  padding: 3rem 4rem;
  box-sizing: border-box;
  @include breakpoint(bp('desktop')) {
    position: absolute;
    padding: 0;
    z-index: 2;
    width: 100%;
    text-align: left;
    top: 50%;
    transform: translateY(-50%);
  }
}

.feature__heading {
  line-height: 1.5;
  color: $black;
}

.feature__subhead {
  font-weight: 400;
  line-height: 1.5;
  text-transform: uppercase;
  color: $dark-gray;
}

.feature__teaser {
  font-weight: 400;
  line-height: 1.5;
  margin-bottom: 1.25em;
}

// 50/50 Split sections:
.feature--50-50 {
  .feature__image {
    width: 100%;
    @include breakpoint(bp('desktop')) {
      width: 50%;
      float: left;
    }
  }
  .feature__text {
    width: 100%;
    @include breakpoint(bp('desktop')) {
      background: $white;
      width: 50%;
      padding: 0 10%;
      position: relative;
      float: left;
      box-sizing: border-box;
      .feature__heading {
        color: $black;
      }
      .feature__subhead {
        color: $dark-gray;
      }
      .feature__teaser {
        color: $dark-gray;
      }
    }
  }
}

.feature--left {
  .feature__image {
    @include breakpoint(bp('desktop')) {
      float: right;
    }
  }
  .feature__text {
    @include breakpoint(bp('desktop')) {
      float: left;
    }
  }
}

// Full width sections
.feature--full {
  @include breakpoint(bp('desktop')) {
    height: 1000px;
  }
  .feature__image {
    width: 100%;
    z-index: 1;
    @include breakpoint(bp('desktop')) {
      height: 1000px;
    }
  }

  .feature__text {
    .feature__heading,
    .feature__teaser {
      color: $white;
    }
    .feature__subhead { color: $black; }
    @include breakpoint(bp('desktop')) {
      width: 40%;
      left: 30%;
    }
  }
}

.feature--full.short {
  height: 250px;

  @include breakpoint(bp('desktop')) {
    height: 625px;
    .feature__image {
      @include breakpoint(bp('desktop')) {
        height: 625px;
      }
    }
  }
}

