section {
    padding: 3rem 2rem;
    @include clearfix;

    @include breakpoint(bp('tablet')) {
        padding: 5rem;
    }
    @include breakpoint(bp('desktop')) {
        padding: 5rem 10rem;
    }
}

section h2.kicker {
    text-transform: uppercase;
    font-weight: 300;
    font-size: 1rem;
    letter-spacing: 0.075em;
}

section p.intro {
    font-size: 2rem;
    line-height: 1.5;
}
