// @file
// Styles for buttons.

.button {
  @include button();

  &.disabled {
    background: #f2f2f2;
    color: $dark-gray;
  }
}

.button--primary {
  @include button($bg: $red);
}

.button--secondary {
  @include button($bg: $dark-gray);
}

.button--more {
  @include button($bg: transparent, $border: true);

  &:hover {
    color: $black;
    border: 2px solid $black;
  }
}

.back {
  @include button($bg: #f2f2f2, $border-radius: 50%);
  color: $red;
  display: block;
  padding: 0;
  text-align: center;
  height: 60px;
  width: 60px;
  line-height: 70px;
}
