header {
  padding: 3rem 2rem;
  @include clearfix;

  @include breakpoint(bp('tablet')) {
      padding: 5rem;
  }
  @include breakpoint(bp('desktop')) {
      padding: 10rem;
  }

  h1 {
    text-transform: uppercase;
    font-weight: 400;
    font-size: em(28px);
    letter-spacing: 0.075em;
    margin-bottom: 0.5em;
  }
  h2 {
    text-transform: uppercase;
    font-weight: 300;
    font-size: em(18px);
    letter-spacing: 0.075em;
    color: #bbb;
  }
}
