// @file
// Layout variables
$max-width: 1200px;

//
$space: 1rem;
$pad: 1rem;

//HiDPI
$hidpi: min-resolution 1.5dppx;

//Breakpoint Map
$bpMap: (
  small: 400px,
  phablet: 600px,
  tablet: 700px,
  ideas-grid: 800px,
  footer-fixes: 900px,
  desktop: 980px,
  mega: 1300px
);

//Get Breakpoint Size
@function bp($key) {
  @if map-has-key($bpMap, $key) {
    @return map-get($bpMap, $key);
  }
}

//Map for z-index
$zIndexMap: (
  low: 2,
  lower: 1,
  lowest: -1 ,
  high: 10,
  higher: 100,
  highest: 10000
);

//Returns z-index setting for key
@function zIndex($key) {
  @if map-has-key($zIndexMap, $key) {
    @return map-get($zIndexMap, $key);
  }
}
