/* Common Grid Block Styling */
@mixin grid-block($height) {
    position: relative;
    height: $height;
    overflow: hidden;
    @extend %clearfix;
}

/* Common Image Styling */
@mixin grid-block__image {
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    position: relative;
    z-index: 1;
}

%grid-block__image  {
  @include grid-block__image ;
}

/* Common Text Block Styling */
@mixin grid-block__text {
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    z-index: zIndex('high');
}

%grid-block__text  {
  @include grid-block__text;
}
