// @file
// Typography variables
// http://sass-lang.com/docs/yardoc/file.SASS_REFERENCE.html#variables_
$base-font-family: "Roboto", "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
$header-font-family:  "Roboto Condensed", "Roboto", "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
$monospace-font-family: Consolas, "Lucida Console", "Liberation Mono", "Courier New", monospace, sans-serif;
$base-line-height: 1.5;
$base-font-size: 18px;
$font-size-normal: $base-font-size;

// Buttons
$button-font-family: $base-font-family;

// Forms
$form-text-size: $font-size-normal;

// Mobile Menu
$mobile-menu-font-family: $header-font-family;

$fontScaleMap: (
  'mega': 2.75em,
  'h1': 2.25em,
  'h2': 1.875em,
  'h3': 1.5em,
  'h4': 1.25em,
  'h5': 1.125em,
  'h6': 1em,
  'small': .755em,
  'mini': .5em
);

@function fontScale($key) {
  @if map-has-key($fontScaleMap, $key) {
    @return map-get($fontScaleMap, $key);
  }
}


$p-font-sizes: (
    null: 16px,
    tablet : 17px,  /* 641px */
    desktop: 18px,   /* 1025px */
    mega: 22px, /* 1921px */
);

$h1-font-sizes: (
    null: 36px,
    tablet : 48px,   /* 641px */
    desktop : 60px,    /* 1025px */
);

$h2-font-sizes: (
    null: 30px,
    tablet: 42px,   /* 641px */
    desktop: 48px,    /* 1025px */
);

$h3-font-sizes: (
    null: 24px,
    tablet: 34px,   /* 641px */
    desktop: 40px,    /* 1025px */
);

$h4-font-sizes: (
    null: 20px,
    tablet: 26px,   /* 641px */
    desktop: 28px,    /* 1025px */
);

$h5-font-sizes: (
    null: 18px,
    tablet: 20px,
);

$h6-font-sizes: (
    null: 16px,
    desktop: 18px,
);
