body {
  font-family: $base-font-family;
}

p {
  font-family: $base-font-family;
  line-height: 1.75;
}

a {
  color: $red;
  font-weight: 600;
  text-decoration: none;
  border-bottom: 1px solid $white;
  transition: all 225ms $ease-smooth;

  &:hover {
    color: $black;
    border-bottom: 1px solid $black;
  }
}

%heading {
  font-family: $header-font-family;
  hyphens: none;
  line-height: 1.25em;
  text-rendering: optimizeLegibility;
}

h1, %h1 {
  @extend %heading;
  font-weight: 600;
  font-size: 2.25em;
  text-transform: uppercase;
  margin: 0 0 1em;
}

h2, %h2 {
  @extend %heading;
  font-size: 1.875em;
  margin: 0 0 1em;
}

h3, %h3 {
  @extend %heading;
  font-size: 1.5em;
  margin: 0 0 1em;
}

h4, %h4 {
  @extend %heading;
  font-size: 1.25em;
  margin: 0 0 0.5em;
}

h5, %h5 {
  @extend %heading;
  font-size: 1.125em;
  margin: 0 0 0.5em;
}

h6, %h6 {
  @extend %heading;
  font-size: 1em;
  margin: 0 0 0.5em;
}
