// @file
// Button helpers

@mixin button($bg: $red, $text: $white, $font-size: 0.875em, $line-height: 30px, $border-radius: 0, $circular: false, $shadow: false, $border: false) {
  background: $bg;
  border-radius: $border-radius;
  color: $text;
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-family: $base-font-family;
  font-size: $font-size;
  font-weight: 600;
  line-height: $line-height;
  margin: 0;
  padding:  0.0125em 1em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 250ms $ease-smooth;
  vertical-align: baseline;
  border: 0;

  @if $circular == true {
    display: block;
    line-height: $line-height;
    height: $line-height;
    width: $line-height;
    border-radius: 50%;
  }

  @if $shadow == true {
    box-shadow: rgba(0, 0, 0, 0.2), 0 1px 2px;
  }

  @if $border == true {
    border: 2px solid $white;
  }

  @if support-legacy-browser('ie', '6', '7') {
    *line-height: normal;
    *zoom: 1;
  }

  &:focus,
  &:hover {
    background-color: darken($bg, 15%);
    border-color: darken($bg, 50%);
    color: $text;
    outline: 0;
  }

  &:active {
    background-color: darken($bg, 15%);
    border-color: darken($bg, 30%);
  }
}

%button {
  @include button();

  & + & {
    margin-left: 1em; // LTR

    [dir="rtl"] & {
    margin-left: 0;
    margin-right: 1em;
    }
  }
}
